import React, { Component } from 'react'
import Home from './SiteHome/Home'

class App extends Component {

  constructor (props) {
    super(props)
    this.state = {
      logo: false
    }
  }

  render() {
    return (
          <div>
          {this.state.logo ?
            <div />
            :
            <Home />

          }
          </div>
    )
  }
}

export default App



